<h1 id="using-sqlite-in-node-js" tabindex="-1"><a class="header-anchor" href="#using-sqlite-in-node-js" aria-hidden="true">#</a> Using SQlite in Node.js</h1>
<p>So, how do we use SQLite in a Node.js application? Let's find out!</p>
<iframe width="560" height="314" src="https://www.youtube.com/embed/65f7Fl0AEi8" frameborder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
<div class="custom-container warning"><p class="custom-container-title">Note!</p>
<p>When communicating with an SQLite 3 database, foreign key constraints are disabled by default! To enable them, you must send the query <code>PRAGMA foreign_keys = ON</code> to the database after you have established a connection with it.</p>
<p>Learn more about this in <a href="https://www.sqlite.org/foreignkeys.html#fk_enable" target="_blank" rel="noopener noreferrer">the documentation for enabling foreign key constraints in SQLite<OutboundLink/></a>.</p>
</div>
